import { h, Component } from 'preact';
import style from './style';
import loadComponents from './components';

export default class Profile extends Component {
  componentWillMount() {
    loadComponents();
  }

  detectControlers = () => {
    return (
      <div>
        <a-entity id="teleHand" hand-controls="left" />
        <a-entity id="blockHand" hand-controls="right" />
      </div>
    );
    // TODO: Check for rift controlers;
    // <a-entity oculus-touch-controls="hand: left"></a-entity>
    // <a-entity oculus-touch-controls="hand: right"></a-entity>
  }

  render() {
    const controlers = this.detectControlers();
    return (
      <a-scene>
        <a-assets>
          <img id="skyTexture" src="https://cdn.aframe.io/a-painter/images/sky.jpg" />
          <img id="groundTexture" src="https://cdn.aframe.io/a-painter/images/floor.jpg" />
          <a-mixin id="voxel"
            geometry="primitive: box; height: 0.5; width: 0.5; depth: 0.5"
            material="shader: standard"
            random-color
            snap="offset: 0.25 0.25 0.25; snap: 0.5 0.5 0.5" />
        </a-assets>
        <a-entity geometry="primitive: box; depth: 0.5; height: 0.5; width 0.5"
          material="shader: standard"
          position="0 0.5 -2"
          random-color />
        <a-cylinder
          id="ground"
          src="https://cdn.aframe.io/a-painter/images/floor.jpg"
          radius="32"
          height="0.1"
        />
        <a-sky
          id="background"
          src="#skyTexture"
          theta-length="90"
          radius="30"
        />

        <a-entity mixin="voxel" position="-1 0 -2" />
        <a-entity mixin="voxel" position="0 0 -2" />
        <a-entity mixin="voxel" position="0 1 -2">
          <a-animation attribute="rotation" to="0 360 0" repeat="indefinite" />
        </a-entity>
        <a-entity mixin="voxel" position="1 0 -2" />
        { controllers }
      </a-scene>
    );
  }
}



